import React from "react"
import Fade from "react-reveal/Fade"

import styles from "./experienceList.module.scss"
import "./experiences"
import experiences from "./experiences"

const experienceList = () => {
    return (
        <section name="experience" className={styles.container}>
          <article>
            <Fade top duration={1000} distance="10%">
              <h1 className={styles.header}>Experiences</h1>
            </Fade>
            <ul className={styles.list}>
              {experiences.map(experience => {
                return (
                    <li className={styles.section}>
                        <Fade top delay={30 % 0} duration={500} distance="10%">
                            <h1 className={styles.title}>{experience.title}</h1>
                            <p className={styles.company}>Company: {experience.company}</p>
                            <p className={styles.technologies}>Technologies: {experience.technologies}</p>
                            <p className={styles.contrabution}>Responsibilities: <br/> {experience.contrabution}</p>
                        </Fade>
                    </li>
                )
              })}
            </ul>
          </article>
        </section>
    )
}

export default experienceList
